.support-container {
  background-color: #f7f9fc;
  max-width: 100%;
  margin-top: 40px;
  z-index: -200;
}

.support-header {
  padding-top: 50px;

  margin-top: 70px;
  font-weight: 700;
  text-align: center;
  font-size: 36px;
  color: #222b45;
}
.support-header__info {
  text-align: center;
  margin-top: 19px;
  color: #192038;
  font-size: 24px;
  font-weight: 400;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 115px;
  padding-bottom: 50px;
}

.support-mobile {
  display: none;
}
@media only screen and (max-width: 600px) {
  .support-header {
    display: none;
  }
  .support-header__info {
    display: none;
  }
  .support-mobile {
    display: block;
    margin-bottom: -50px;
  }
  .support-header__mobile {
    text-align: center;
    padding-top: 50px;
    padding-bottom: 20px;
    font-size: 28px;
    line-height: 34px;
    font-weight: bold;
    color: #222b45;
    margin: 0 auto;
  }
  .support-header__infoMobile {
    margin: 0 auto;
    text-align: center;
    font-size: 18px;
    line-height: 140%;
    color: #192038;
    padding: 0 20px;
  }
}
